import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"




const Linux = () => {
    const data = useStaticQuery(graphql`
    query {
        robotMaceta1: file(relativePath: { eq: "robotMaceta1.jpg" }) {
        childImageSharp {
          fluid (quality: 100){
            ...GatsbyImageSharpFluid
          }
        }
      }
      robotMaceta2: file(relativePath: { eq: "robotMaceta2.jpg" }) {
        childImageSharp {
          fluid (quality: 100){
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
    return(
  <Layout>
    <SEO  
    title="Descarga música para linux" 
    meta="El mejor programa para descargar música para linux. Te explicamos que programa hemos elegido y por que. También te damos los enlaces para que te lo descargues"
    description="El mejor programa para descargar música para linux. Te explicamos que programa hemos elegido y por que. También te damos los enlaces para que te lo descargues"
    lang="es"
    />

<div class="pageHeadLines">
      <h2>Instala youtube-dl para linux</h2>

      <p>YouTube-dl est&aacute; disponible como un binario empaquetado en muchos sistemas operativos Linux. Por ejemplo, en los sistemas Debian o Ubuntu, puede instalarse usando apt:</p>
    
    <div class="codeBlock">
    sudo apt install youtube-dl
    </div>
  
    <p style={{marginTop:`50px`}}>En todos los sistemas Linux, también puedes instalarlo usando el curl, wget</p>
    <p>Para descargar un archivo de video, simplemente ejecute el siguiente comando. Donde "VIDEO_URL" es la url del video que quieres descargar.</p>
    <div class="codeBlock">
    youtube-dl https://www.youtube.com/watch?v=iRYvuS9OxdA
    </div>
    <p>Para listar todos los formatos de video disponibles, use la opción "-list-formats" con el comando youtube-dl.</p>
    <div class="codeBlock">
    youtube-dl --list-formats https://www.youtube.com/watch?v=iRYvuS9OxdA
    </div>
    <p>Para descargar un formato de archivo preferido, use la opción '-f' (código de formato de video). Por ejemplo, me gustaría descargar el formato mp4, así que uso el código de formato como '18' como se muestra a continuación.</p>
    <div class="codeBlock">
    youtube-dl -f 18 https://www.youtube.com/watch?v=iRYvuS9OxdA
    </div>
    <p>Para descargar una lista de archivos de vídeo, crea un archivo y coloca todos los enlaces de YouTube que desees descargar.</p>
    </div>
    <div class="codeBlock">
    youtube-dl -a youtube_links.txt
    </div>
    <div style={{ height:`300px`}}>
    </div>
  </Layout>
    )
    }

export default Linux
